.portafolio{
    margin-top: 200px;
    text-align: center;
    
}

.portafolio a{
    color: white;

    text-decoration: none;
    font-size: 4em;

}

.portafolio li{
    margin-bottom: 100px;
    transition: all 1s;
}

.portafolio li:hover{
    transform: scale(1.5);
    
}

.video_portafolio{
    position: fixed;
    width: 100%;
     top: 80px;
    /* left: 50%; */
    /* transform: translate(-50%) translateY(-50%); */
    z-index: -1;
    
}
/* height: 100%; */
.video_portafolio::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
     /* background: #031d589d; */
     background-color:#69cee5f8;
  z-index: 1;
}